import common from '../../common/common.js'
import util from '../../common/util.js'
	
export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			shopid:0,				//店铺ID
			shop:[],				//店铺信息			
			goodsList:[],			//店铺商品列表						
			bannerList:[],			//广告列表
			catList:[],				//分类列表
			adsList:[],				//广告列表
			coupon:[],				//优惠券

			menu:{
				mindex:0,
				shopid:0,
				shop:[],
			},
			
			info:{
				bg_color:'0,170,250',
				bg_alpha:1,
				delta:2,
				icon_color:'white',
				shpid:0,
			},
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			mainTop:0,
			
			popsharehb: false,
			shareImg:'',
			
			showForm:false,
			
		}
	},
	onLoad(option) {
		var that = this
		var shopid = option.id
		// shopid = 1;
				
		if(shopid > 0){
			that.menu.shopid = shopid
			that.info.shopid = shopid
			that.shopid = shopid
			uni.setStorageSync('shopid', shopid);
			//获取店铺信息
			that.getShopInfo();
			//获取店铺优惠券
			that.getShopCoupon();	
			//获取店铺广告列表
			that.getShopAds();
			//获取店铺商品信息
			that.getShopCommendGoodsList();
			
		}
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		
		var pages = getCurrentPages();
		that.info.delta = pages.length
		
		
		that.mainTop = common.titleHeight + 40;
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		
		//获取店铺信息
		that.getShopInfo();
		//获取店铺优惠券
		that.getShopCoupon();
		//获取店铺广告列表
		that.getShopAds();
		//获取店铺推荐商品信息
		that.getShopCommendGoodsList();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') {// 来自页面内转发按钮
		  console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		var shopid = that.shopid
		if(userInfo.users_id > 0){
			var shareUrl = '/pages/merchant/shop?id='+shopid+'&tjuid='+userInfo.users_id
		}else{
			var shareUrl = '/pages/merchant/shop?id='+shopid
		}
		return {
		  title: that.shop.shop_title,
		  path: shareUrl,
		}
	},
	methods: {
		//领取优惠券
		linquCoupon(){
			var that = this
			var coupon = that.coupon
			var userInfo = uni.getStorageSync('userInfo');
			if(!userInfo){
				that.checkLogin();
				return false
			}
			uni.showModal({
				title: '提示',
				content: '确定要用'+that.coupon.c_integral+'积分兑换吗？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if(res.confirm){
						uni.request({
							url: common.apiUrl + 'shop/linquCoupon',
							method: 'GET',
							data: {
								userid:userInfo.users_id,
								shopid:that.shopid,
								couponid:that.coupon.c_id
							},
							success: res => {
								if(res.data.code == 0){
									uni.showModal({
										title: '提示',
										content: '领取成功,请在"我的>优惠"中查看',
										showCancel: true,
										cancelText: '取消',
										confirmText: '确定',
										success: res => {
											if(res.confirm){
												uni.switchTab({
													url: '/pages/user/user'
												});
											}
											that.getUserInfo();
										},
										fail: () => {},
										complete: () => {}
									});
									
								}else{
									uni.showToast({title:res.data.msg,icon:'none',duration:1500})
								}					
								
							},
							fail: () => {},
							complete: () => {}
						});
					}
				},
			});
			
		},
		//获取用户信息
		getUserInfo(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			if(userInfo.users_id > 0){
				uni.request({
					url: common.apiUrl+'user/getUserInfo',
					method: 'GET',
					data: {
						userid:userInfo.users_id
					},
					success: res => {
						if(res.data.code == 0){
							var userInfo = res.data.userInfo;
							uni.setStorageSync('userInfo',userInfo);
							that.userInfo = userInfo
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.showModal({
					title: '提示',
					content: '你还没登录，现在去登录吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if(res.confirm){
							uni.navigateTo({
								url: '/pages/userlogin/userlogin',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		//用户登录
		checkLogin(){
			var that = this
			var userid = uni.getStorageSync('userid');
			if(!userid){
				console.log('还没有登录');
				uni.navigateTo({
					url: '/pages/userlogin/userlogin',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}else{
				console.log('已经登录');
			}
		},
		//电话联系
		phoneContact(e){
			var that = this
			var index = e.currentTarget.dataset.index
			var shopList = that.commendShopList
			var mobile = e.currentTarget.dataset.mobile
			uni.makePhoneCall({
			    phoneNumber: mobile 
			});
		},
		//导航
		daohang(){
			var that = this
			var shop = that.shop
			console.log(shop)
			uni.openLocation({
			    latitude: parseFloat(shop.shop_lat),
			    longitude: parseFloat(shop.shop_lng),
			    success: function () {
			        console.log('success');
			    }
			});
		},
		
		
		
		//获取店铺推荐商品信息
		getShopCommendGoodsList(){
			var that = this
			var shopid = that.shopid
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopCommendGoodsList',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {			
					that.goodsList = res.data.goods_list	
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//跳转
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.navigateTo({
				url:url,
			});
		},
		//获取店铺信息
		getShopInfo(){
			var that = this
			var shopid = that.shopid
			var userInfo = uni.getStorageSync('userInfo');
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopInfo',
				method: 'GET',
				data: {
					shopid:shopid,
					userid:userInfo.users_id
				},
				success: res => {
					var shop = res.data.shop
					that.shop = res.data.shop

					that.menu.shop = res.data.shop
					uni.setNavigationBarTitle({
					    title: shop.shop_title
					});
					that.info.title = shop.shop_title
					that.bannerList = shop.shop_pics
					console.log('bannerList',that.bannerList)
	
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取店铺优惠券
		getShopCoupon(){
			var that = this
			var shopid = that.shopid
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopCoupon',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.coupon = res.data.coupon
					console.log("coupon",that.coupon)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取店铺广告
		getShopBanner(){
			var that = this
			var shopid = that.shopid
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopBanner',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.bannerList = res.data.banner_list
					console.log('banner_list',res.data)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取店铺广告列表
		getShopAds(){
			var that = this
			var shopid = that.shopid
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopAds',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.adsList = res.data.ads_list
					console.log('ads_list',res.data)
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}



